import { PropsWithChildren } from 'react';

import type { TypographyProps } from 'components/ui/Typography';

import Typography from 'components/ui/Typography';

import classNames from 'classnames';
import './index.scss';

// ----------------------------------------------------------------------

export interface TitleAreaProps extends PropsWithChildren {
  flexDirection?: 'row' | 'column';
  mb?: string;
  hasSeparator?: boolean;
}

export interface TitleProps extends PropsWithChildren {
  size?: TypographyProps['size'];
}

const FLEX_DIRECTION_MAPS = {
  row: 'qb-title-area--row',
  column: 'qb-title-area--column'
};

export const Title = ({ size = 'sm', children }: TitleProps) => {
  return (
    <Typography variant="head" size={size} className="qb-title-area__head">
      {children}
    </Typography>
  );
};

export const Description = ({ children }: PropsWithChildren) => {
  return (
    <Typography size="xs" className="qb-title-area__description">
      {children}
    </Typography>
  );
};

const TitleArea = ({
  flexDirection = 'row',
  mb = 'mb-8',
  hasSeparator = true,
  children
}: TitleAreaProps) => {
  return (
    <div
      className={classNames(
        'qb-title-area',
        FLEX_DIRECTION_MAPS[flexDirection],
        {
          'qb-title-area--has-separator': flexDirection === 'row' && hasSeparator
        },
        mb
      )}
    >
      {children}
    </div>
  );
};

TitleArea.Title = Title;
TitleArea.Description = Description;

export default TitleArea;
